@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap);
*{
    box-sizing: border-box;
}
body {
   background-color: #111;
   color: #fff;
   margin: 0;
   font-family: 'Roboto', sans-serif;;
}
.featured {
    height: 100vh;
}
.featured--vertical {
    width: inherit;
    height: inherit;
    background: -webkit-gradient(linear, left bottom, left top, color-stop(10%, #111), color-stop(90%, transparent));
    background: linear-gradient(to top, #111 10%, transparent 90%);
}
.featured--horizontal{
    width: inherit;
    height: inherit;
    background: -webkit-gradient(linear, left top, right top, color-stop(30%, #111), color-stop(70%, transparent));
    background: linear-gradient(to right, #111 30%, transparent 70%);
}
.featured--name{
    font-size: 60px;
    font-weight: bold;
}
.featured--info{
    font-size: 18px;
    font-weight: bold;
    margin-top: 15px;

}
.featured--points{
    display: inline-block;
    color: #46d369;
    margin-right: 15px;
}
.featured--year{
    display: inline-block;
    margin-right: 15px;
}
.featured--seasons{
    display: inline-block;
}
.movieRow{
    margin-bottom: 30px;
}
.movieRow h2{
    margin: 0px 0px 0px 30px;
}
.movieRow--listarea{
    overflow-x: hidden;
    padding-left: 30px;
}
.movieRow--list{
    width: 9999999px;
}
.movieRow--item{
    display: inline-block;
    width: 150px;
    cursor: pointer;
}
.movieRow--item img{
    width: 100%;
    -webkit-transform: scale(0.9);
            transform: scale(0.9);
    -webkit-transition: all ease 0.2s;
    transition: all ease 0.2s;
}
.movieRow--item img:hover {
    -webkit-transform: scale(1);
            transform: scale(1);
}

