.featured {
    height: 100vh;
}
.featured--vertical {
    width: inherit;
    height: inherit;
    background: linear-gradient(to top, #111 10%, transparent 90%);
}
.featured--horizontal{
    width: inherit;
    height: inherit;
    background: linear-gradient(to right, #111 30%, transparent 70%);
}
.featured--name{
    font-size: 60px;
    font-weight: bold;
}
.featured--info{
    font-size: 18px;
    font-weight: bold;
    margin-top: 15px;

}
.featured--points{
    display: inline-block;
    color: #46d369;
    margin-right: 15px;
}
.featured--year{
    display: inline-block;
    margin-right: 15px;
}
.featured--seasons{
    display: inline-block;
}